import { Stack, Typography } from '@mui/material'

import { type ReviewKeyword as ReviewKeywordType } from 'src/responses/review'

interface ReviewKeywordProps {
  keyword: ReviewKeywordType
  isSelected?: boolean
  onClick?: () => void
  selectable?: boolean
}

const ReviewKeyword = ({ keyword, isSelected = false, onClick, selectable = false }: ReviewKeywordProps) => {
  return (
    <Stack
      direction="row"
      alignItems="center"
      spacing="10px"
      onClick={selectable ? onClick : undefined}
      maxWidth="232px"
      minHeight="41px"
      sx={{
        border: selectable ? (isSelected ? '1px solid #1A75FF' : '1px solid #CCCCCC') : '1px solid #CCCCCC',
        borderRadius: '10px',
        padding: selectable ? '4px 12px' : '4px 8px',
        cursor: selectable ? 'pointer' : 'default',
        backgroundColor: selectable && isSelected ? 'rgba(85, 105, 255, 0.1)' : '#FFFFFF',
        transition: 'all 0.2s',
        '&:hover': selectable
          ? {
              backgroundColor: isSelected ? 'rgba(85, 105, 255, 0.2)' : 'rgba(0, 0, 0, 0.04)'
            }
          : undefined
      }}
    >
      <Typography
        sx={{
          fontSize: '13px',
          lineHeight: 1,
          minWidth: selectable ? '24px' : '34px',
          textAlign: 'center'
        }}
      >
        {keyword.icon}
      </Typography>
      <Typography
        sx={{
          fontFamily: 'Pretendard',
          fontSize: '13px',
          color: selectable && isSelected ? '#1A75FF' : '#000000',
          lineHeight: 1,
          fontWeight: selectable && isSelected ? 700 : 400
        }}
      >
        {keyword.label}
      </Typography>
    </Stack>
  )
}

export default ReviewKeyword
