import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { type UseMutationResult, useMutation, useQuery, useQueryClient } from 'react-query'

import { COMMON_MESSAGE, REVIEW_MESSAGE } from 'src/data/messages'

import { type ClubId, type ReviewId } from 'src/models/club'
import { type Semester } from 'src/models/date'

import { type GetClubReviewResponseData, type ReviewKeyword } from 'src/responses/review'

interface PostReviewRequestBody {
  content: string
  year: number
  semester: Semester
  recommendedPerson: string
  keywordIds: number[]
}

export interface PostReviewParams extends PostReviewRequestBody {
  clubId: ClubId
}

const postReview = async (
  { clubId, content, year, semester, recommendedPerson, keywordIds }: PostReviewParams,
  token: string
) => {
  await axios.post<null, AxiosResponse<null>, PostReviewRequestBody>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/review`,
    { content, year, semester, recommendedPerson, keywordIds },
    {
      headers: { Authorization: token }
    }
  )
}

export const usePostReview = (): UseMutationResult<void, unknown, PostReviewParams, unknown> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PostReviewParams) => postReview(values, token)

  return useMutation(mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('reviews')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error?.response?.data?.message ?? COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

const updateReview = async (
  {
    clubId,
    reviewId,
    content,
    year,
    semester,
    recommendedPerson,
    keywordIds
  }: PostReviewParams & { reviewId: ReviewId },
  token: string
) => {
  await axios.patch(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/review/${reviewId}`,
    { content, year, semester, recommendedPerson, keywordIds },
    {
      headers: { Authorization: token }
    }
  )
}

export const useUpdateReview = (): UseMutationResult<
  void,
  unknown,
  PostReviewParams & { reviewId: ReviewId },
  unknown
> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PostReviewParams & { reviewId: ReviewId }) => updateReview(values, token)

  return useMutation(mutationFn, {
    onSuccess: (data, variables) => {
      enqueueSnackbar('후기를 수정했어요.', { variant: 'success' })
      queryClient.invalidateQueries(['reviews', variables.clubId])
      queryClient.invalidateQueries(['adminReviews', variables.clubId])
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error?.response?.data?.message ?? '후기 수정에 실패했어요.'
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

const deleteReview = async ({ clubId, reviewId }: { clubId: ClubId; reviewId: ReviewId }, token: string) => {
  await axios.delete(`${process.env.REACT_APP_APIHOST}/club/${clubId}/review/${reviewId}`, {
    headers: { Authorization: token }
  })
}

export const useDeleteReview = (clubId: ClubId): UseMutationResult<void, unknown, { reviewId: ReviewId }, unknown> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = ({ reviewId }: { reviewId: ReviewId }) => deleteReview({ clubId, reviewId }, token)

  return useMutation(mutationFn, {
    onMutate: async ({ reviewId }) => {
      await queryClient.cancelQueries(['reviews', clubId])
      const previousReviews = queryClient.getQueryData<GetClubReviewResponseData>(['reviews', clubId])
      if (previousReviews) {
        queryClient.setQueryData<GetClubReviewResponseData>(
          ['reviews', clubId],
          previousReviews.filter(review => review.id !== reviewId)
        )
      }
      return { previousReviews }
    },
    onSuccess: () => {
      enqueueSnackbar('후기를 삭제했어요.', { variant: 'success' })
      queryClient.invalidateQueries(['reviews', clubId])
      queryClient.invalidateQueries(['adminReviews', clubId])
    },
    onError: (error, variables, context: any) => {
      if (context?.previousReviews) {
        queryClient.setQueryData(['reviews', clubId], context.previousReviews)
      }
      if (error instanceof AxiosError) {
        const errorMessage = error?.response?.data?.message ?? '후기 삭제에 실패했어요.'
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

