export const useDeleteMyReview = (): UseMutationResult<
  void,
  unknown,
  { clubId: ClubId; reviewId: ReviewId },
  unknown
> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = ({ clubId, reviewId }: { clubId: ClubId; reviewId: ReviewId }) =>
    deleteReview({ clubId, reviewId }, token)

  return useMutation(mutationFn, {
    onMutate: async ({ reviewId }) => {
      await queryClient.cancelQueries(['userReviews'])
      const previousReviews = queryClient.getQueryData<GetClubReviewResponseData>(['userReviews'])
      if (previousReviews) {
        queryClient.setQueryData<GetClubReviewResponseData>(
          ['userReviews'],
          previousReviews.filter(review => review.id !== reviewId)
        )
      }
      return { previousReviews }
    },
    onSuccess: () => {
      enqueueSnackbar('후기를 삭제했어요.', { variant: 'success' })
      queryClient.invalidateQueries(['userReviews'])
    },
    onError: (error, variables, context: any) => {
      if (context?.previousReviews) {
        queryClient.setQueryData(['userReviews'], context.previousReviews)
      }
      if (error instanceof AxiosError) {
        const errorMessage = error?.response?.data?.message ?? '후기 삭제에 실패했어요.'
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

