import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useLocation, useNavigate, useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { Box, Card, Container, Stack, TextField, Typography } from '@mui/material'
import InputAdornment from '@mui/material/InputAdornment'
import { inputBaseClasses } from '@mui/material/InputBase'

import { AUTH_USER_MESSAGE, REVIEW_MESSAGE } from 'src/data/messages'

import type { ClubMemberForAuth } from 'src/models/clubMember'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import DateForm from 'src/components/DateForm'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import ReviewKeyword from 'src/components/ReviewKeyword'

import { type PostReviewParams, useGetReviewKeywords, usePostReview, useUpdateReview } from 'src/hooks/api/review'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'
import { type ClubReview } from 'src/responses/review'

const ReviewPage: React.FC = () => {
  const { id } = useParams()
  const clubId = Number(id)
  const reviewDate = useRecoilValue(dateState)

  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const location = useLocation()
  const { userInfo, isLoggedIn } = useSafeAuthUser()

  const userClub = userInfo?.clubs.find(c => c.id === clubId) as ClubMemberForAuth
  const reviewToEdit = (location.state as any)?.review as ClubReview | undefined

  const { data: keywords } = useGetReviewKeywords(clubId)
  const postReviewMutation = usePostReview()
  const updateReviewMutation = useUpdateReview()

  const [reviewData, setReviewData] = useState<PostReviewParams>({
    clubId: clubId,
    content: reviewToEdit?.content ?? '',
    recommendedPerson: reviewToEdit?.recommendedPerson ?? '',
    year: reviewToEdit?.year ?? reviewDate.year,
    semester: reviewToEdit?.semester ?? reviewDate.semester,
    keywordIds: reviewToEdit?.keywords?.map(k => k.id) ?? []
  })

  useEffect(() => {
    setReviewData(prev => ({
      ...prev,
      year: reviewDate.year,
      semester: reviewDate.semester
    }))
  }, [reviewDate])

  useEffect(() => {
    if (isLoggedIn && !userInfo) {
      enqueueSnackbar(AUTH_USER_MESSAGE.WRONG_AUTH_STATE, { variant: 'error' })
      navigate('/', { replace: true }) // 히스토리 남기지 않음
    }
  }, [isLoggedIn, userInfo, enqueueSnackbar, navigate])

  useEffect(() => {
    if (!userClub) {
      enqueueSnackbar(REVIEW_MESSAGE.WRONG_ACCESS, { variant: 'error' })
      navigate('/', { replace: true })
    }
  }, [userClub, enqueueSnackbar, navigate])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setReviewData(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleKeywordToggle = (keywordId: number) => {
    setReviewData(prev => {
      const isSelected = prev.keywordIds.includes(keywordId)
      if (isSelected) {
        return {
          ...prev,
          keywordIds: prev.keywordIds.filter(id => id !== keywordId)
        }
      } else {
        const MAX_KEYWORD_SELECTION = 3
        if (prev.keywordIds.length >= MAX_KEYWORD_SELECTION) {
          return prev
        }
        return {
          ...prev,
          keywordIds: [...prev.keywordIds, keywordId]
        }
      }
    })
  }

  const handleSubmit = () => {
    const MAX_KEYWORD_SELECTION = 3
    const MIN_REVIEW_CONTENT_LENGTH = 50
    const MAX_REVIEW_CONTENT_LENGTH = 1000
    const MAX_RECOMMENDED_PERSON_LENGTH = 30

    if (reviewData.keywordIds.length > MAX_KEYWORD_SELECTION) {
      enqueueSnackbar(`키워드는 최대 ${MAX_KEYWORD_SELECTION}개까지 선택 가능합니다.`, { variant: 'warning' })
      return
    }
    if (
      reviewData.content.length < MIN_REVIEW_CONTENT_LENGTH ||
      reviewData.content.length > MAX_REVIEW_CONTENT_LENGTH
    ) {
      enqueueSnackbar('후기 내용은 50자 이상 1000자 이내로 작성해주세요.', { variant: 'warning' })
      return
    }
    if (reviewData.recommendedPerson.length > MAX_RECOMMENDED_PERSON_LENGTH) {
      enqueueSnackbar('추천 대상은 30자 이내로 작성해주세요.', { variant: 'warning' })
      return
    }

    if (reviewToEdit) {
      updateReviewMutation.mutate(
        { ...reviewData, reviewId: reviewToEdit.id },
        {
          onSuccess: () => {
            navigate(`/club/${clubId}/profile`, { replace: true })
          }
        }
      )
    } else {
      postReviewMutation.mutate(reviewData, {
        onSuccess: () => {
          navigate(`/club/${clubId}/profile`, { replace: true })
        }
      })
    }
  }

  return (
    <>
      <Helmet>
        <title>{reviewToEdit ? '후기 수정' : '후기 작성'}</title>
      </Helmet>
      <PageTitleWrapper>
        <Typography variant="h3" gutterBottom>
          {userClub?.name}
        </Typography>
        <Typography variant="subtitle2">{reviewToEdit ? '활동 후기 수정' : '활동 후기 작성'}</Typography>
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Card sx={{ padding: 4 }}>
          <Box display={'flex'} sx={{ width: 'auto' }}>
            <Box sx={{ width: 'auto' }}>
              <DateForm club={userClub} />
            </Box>
          </Box>
          <Stack spacing={3} mb={4}>
            <TextField
              required
              fullWidth
              multiline
              minRows={3}
              maxRows={6}
              label="동아리 활동은 어땠나요?"
              variant="outlined"
              onChange={handleChange}
              name="content"
              value={reviewData.content}
            />
            <TextField
              variant="outlined"
              label="이 동아리를 어떤 사람에게 추천하시나요?"
              inputProps={{ maxLength: 15 }}
              InputProps={{
                endAdornment: (
                  <InputAdornment
                    position="end"
                    sx={{
                      opacity: 0,
                      pointerEvents: 'none',
                      [`[data-shrink=true] ~ .${inputBaseClasses.root} > &`]: {
                        opacity: 1
                      }
                    }}
                  >
                    에게 추천!
                  </InputAdornment>
                )
              }}
              onChange={handleChange}
              name="recommendedPerson"
              value={reviewData.recommendedPerson}
            />
            {keywords && keywords.length > 0 && (
              <Box>
                <Typography variant="subtitle1" gutterBottom sx={{ fontWeight: 600 }}>
                  이 동아리를 키워드로 표현한다면? (최대 3개)
                </Typography>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {keywords.map(keyword => {
                    const isSelected = reviewData.keywordIds.includes(keyword.id)
                    return (
                      <ReviewKeyword
                        key={keyword.id}
                        keyword={keyword}
                        selectable
                        isSelected={isSelected}
                        onClick={() => {
                          handleKeywordToggle(keyword.id)
                        }}
                      />
                    )
                  })}
                </Box>
              </Box>
            )}
          </Stack>
          <CancelSubmitBtns
            handleCancel={() => {
              navigate(-1)
            }}
            handleSubmit={handleSubmit}
            submitBtnText={reviewToEdit ? '수정' : '등록'}
          />
        </Card>
      </Container>
    </>
  )
}

export default ReviewPage
